* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  background: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.scroll {
  display: flex;
  position: relative;
  overflow: hidden;
}

.scroll div {
  white-space: nowrap;
  animation: scroll var(--time) linear infinite;
  animation-delay: calc(var(--time) * -1);
}

.scroll div:nth-child(2) {
  animation: scroll2 var(--time) linear infinite;
  animation-delay: calc(var(--time) / -2);
}

@keyframes scroll {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-100%);
  }
}

@keyframes scroll2 {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-200%);
  }
}

.scroll div span {
  letter-spacing: .2em;
  color: #fff;
  background: #333;
  border-radius: 5px;
  margin: 10px;
  padding: 5px 10px;
  transition: all .5s;
  display: inline-flex;
}

.scroll div span:hover {
  cursor: pointer;
  background: #3fd2f9;
}

.imgBox div {
  justify-content: center;
  align-items: center;
  display: flex;
}

.imgBox img {
  max-width: 400px;
  max-height: 300px;
}
